.ImageCanva {
  width: 100%;
  background: linear-gradient(45deg, rgb(105, 175, 255), rgb(234, 0, 255));
  padding: 2% 0;
}
.ImageCanva h1 {
  text-align: center;
  font-size: 4rem;
  color: aliceblue;
  margin-bottom: 1em;
}
.ImageCanva .ImgCanvaCard {
  width: 70%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2%;
  margin: auto;
  background: rgba(255, 247, 247, 0.25);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37), 0 8px 32px 0 rgba(0, 0, 0, 0.37), 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  backdrop-filter: blur(10.5px);
  -webkit-backdrop-filter: blur(10.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.ImageCanva .ImgCanvaCard .ImgCanvaCardPic {
  position: relative;
  width: 500em;
}
.ImageCanva .ImgCanvaCard .ImgCanvaCardPic .canvas {
  cursor: pointer;
}
.ImageCanva .ImgCanvaCard .ImgCanvaCardPic .lds-circle {
  transform: translateZ(1px);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ImageCanva .ImgCanvaCard .ImgCanvaCardPic .lds-circle > div {
  display: inline-block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  background: #dfc;
  animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.ImageCanva .ImgCanvaCard .CanvasOption {
  background-color: aliceblue;
  width: 500em;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 1em;
}
.ImageCanva .ImgCanvaCard .CanvasOption .CaptionTag {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ImageCanva .ImgCanvaCard .CanvasOption .CaptionTag input {
  margin-left: 5em;
  font-size: 1.2rem;
  padding: 0.2em;
  border: none;
  outline: none;
  border-bottom: 0.2em solid black;
  background-color: transparent;
}
.ImageCanva .ImgCanvaCard .CanvasOption .CaptionTag button {
  padding: 0.3em 0.2em;
}
.ImageCanva .ImgCanvaCard .CanvasOption button {
  background-color: black;
  color: aliceblue;
  font-size: 1.5rem;
  width: 10em;
  margin: 1%;
  font-weight: bold;
  border: transparent;
  cursor: pointer;
  transition: 0.3s;
}
.ImageCanva .ImgCanvaCard .CanvasOption button:hover {
  border-radius: 1em;
  color: black;
  background: linear-gradient(45deg, rgb(105, 175, 255), rgb(234, 0, 255));
  transition: 0.3s;
}

@keyframes lds-circle {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(1800deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    transform: rotateY(3600deg);
  }
}/*# sourceMappingURL=imageCanva.css.map */